'use client'

import React from 'react'
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table"
import { Separator } from "@/components/ui/separator"

const BillingPage = () => {
    return (
        <div className="max-w-4xl space-y-6">

            {/* ---------------- Current Plan ---------------- */}
            <Card>
                <CardHeader className="flex flex-row items-center justify-between">
                    <CardTitle>Current Plan</CardTitle>
                    <Badge variant="success">Active</Badge>
                </CardHeader>

                <CardContent className="space-y-3">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-lg font-medium">Pro Plan</p>
                            <p className="text-sm text-muted-foreground">
                                ₹299 / month
                            </p>
                        </div>

                        <Button variant="outline">
                            Change Plan
                        </Button>
                    </div>

                    <Separator />

                    <div className="text-sm text-muted-foreground">
                        Next billing on <strong>25 Sep 2025</strong>
                    </div>
                </CardContent>
            </Card>

            {/* ---------------- Plan Features ---------------- */}
            <Card>
                <CardHeader>
                    <CardTitle>Your Plan Includes</CardTitle>
                </CardHeader>

                <CardContent className="space-y-2 text-sm">
                    <p>✔ Unlimited customers</p>
                    <p>✔ Payment verification</p>
                    <p>✔ Reward management</p>
                    <p>✔ Analytics access</p>
                </CardContent>
            </Card>

            {/* ---------------- Payment History ---------------- */}
            <Card>
                <CardHeader>
                    <CardTitle>Payment History</CardTitle>
                </CardHeader>

                <CardContent>
                    <Table>
                        <TableHeader>
                            <TableRow>
                                <TableHead>Date</TableHead>
                                <TableHead>Amount</TableHead>
                                <TableHead>Status</TableHead>
                                <TableHead>Method</TableHead>
                                <TableHead className="text-right">Invoice</TableHead>
                            </TableRow>
                        </TableHeader>

                        <TableBody>
                            <TableRow>
                                <TableCell>12 Sep 2025</TableCell>
                                <TableCell>₹299</TableCell>
                                <TableCell>
                                    <Badge variant="success">Success</Badge>
                                </TableCell>
                                <TableCell>UPI</TableCell>
                                <TableCell className="text-right">
                                    <Button variant="link">Download</Button>
                                </TableCell>
                            </TableRow>

                            <TableRow>
                                <TableCell>12 Aug 2025</TableCell>
                                <TableCell>₹299</TableCell>
                                <TableCell>
                                    <Badge variant="secondary">Failed</Badge>
                                </TableCell>
                                <TableCell>UPI</TableCell>
                                <TableCell className="text-right">—</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>

            {/* ---------------- Actions ---------------- */}
            <Card>
                <CardHeader>
                    <CardTitle>Manage Subscription</CardTitle>
                </CardHeader>

                <CardContent className="flex gap-3">
                    <Button>Upgrade Plan</Button>
                    <Button variant="outline">Cancel Subscription</Button>
                </CardContent>
            </Card>
        </div>
    )
}

export default BillingPage